<template>
  <v-container fluid >
    <v-layout row wrap justify-center>
      <v-dialog v-model="dialog" persistent max-width="50em">
        <v-card>
          <!-- <v-snackbar v-model="snackbar" absolute top right color="success">
            <span>!</span>
          <v-icon dark>check_circle</v-icon>
          </v-snackbar> -->
          <v-form ref="form" v-model="valid" lazy-validation @submit.prevent="submit">
            <v-container fluid>
              <v-layout row wrap>
                <v-flex d-flex md6>
                  <v-card color="orange" dark>
                    <v-card-text>
                    <!--  -->
                    </v-card-text>
                  </v-card>
                </v-flex>
                <v-flex d-flex md6>
                  <v-layout row wrap>
                    <v-flex d-flex xs12>
                      <v-card color="white">
                         <v-card-text>
                    <!--  -->
                    </v-card-text>
                      </v-card>
                    </v-flex>
                     <v-flex d-flex xs12>
                      <v-card color="white">
                         <v-card-text>
                    <!--  -->
                    </v-card-text>
                      </v-card>
                    </v-flex>
                  </v-layout>
                </v-flex>
                <v-divider></v-divider>
  
                <v-flex d-flex md12>
                  <v-layout row wrap v-if="listname ==''">
                    <v-flex d-flex xs12>
                   <v-text-field label="" prepend-icon="announcement" v-model="form.card.title" :counter="20" :rules="titleRules"></v-text-field>
                    </v-flex>
                  </v-layout>
                  <v-layout row wrap v-if="listname == ''">
                         <v-flex d-flex xs12>
                   <v-text-field label="" prepend-icon="announcement" v-model="form.card.title" :counter="20" :rules="titleRules"></v-text-field>
                    </v-flex>
                     <v-flex d-flex xs12>
                   <v-text-field label="" prepend-icon="people" v-model="form.card.desc" ></v-text-field>
                    </v-flex>
                  </v-layout>
                      <v-layout row wrap  v-if="listname == ''">
                         <v-flex d-flex xs12>
                   <v-text-field label="" prepend-icon="announcement" v-model="form.card.title" :counter="20" :rules="titleRules"></v-text-field>
                    </v-flex>
                      </v-layout>
                      <v-layout row wrap  v-if="listname == ''">
                        <v-flex d-flex xs12>
                   <v-text-field label="" prepend-icon="announcement" v-model="form.card.title" :counter="20" :rules="titleRules"></v-text-field>
                    </v-flex>
                      <v-flex d-flex xs12>
                   <v-text-field label="" prepend-icon="people" v-model="form.card.desc"  ></v-text-field>
                    </v-flex>
                        </v-layout>
                      <v-layout row wrap v-if="listname == ''">
                     <v-flex d-flex xs12>
                   <v-text-field label="" prepend-icon="announcement" v-model="form.card.title" :counter="20" :rules="titleRules"></v-text-field>
                    </v-flex>
                      <v-flex d-flex xs12>
                   <v-text-field label="" prepend-icon="people" v-model="form.card.desc"  ></v-text-field>
                    </v-flex>
                        </v-layout>
                      <v-layout row wrap v-if="listname == ''">
                      <v-flex d-flex xs12>
                   <v-text-field label="" prepend-icon="announcement" v-model="form.card.title" :counter="20" :rules="titleRules"></v-text-field>
                    </v-flex>
                     <v-flex d-flex xs12>
                   <v-text-field label="" prepend-icon="announcement" v-model="form.card.department" ></v-text-field>
                    </v-flex>
                     <v-flex d-flex xs12>
                   <v-text-field label="" prepend-icon="announcement" v-model="form.card.background"  ></v-text-field>
                    </v-flex>
                        </v-layout>
                      <v-layout row wrap v-if="listname == '//'">
                      <v-flex d-flex xs12>
                   <v-text-field label="//" prepend-icon="announcement" v-model="form.card.title" :counter="20" :rules="titleRules"></v-text-field>
                    </v-flex>
                      <v-flex d-flex xs12>
                   <v-text-field label="" prepend-icon="people" v-model="form.card.summary"></v-text-field>
                    </v-flex>
                    <v-flex d-flex xs12>
                   <v-text-field label="" prepend-icon="people" v-model="form.card.induction" ></v-text-field>
                    </v-flex>
                        </v-layout>
                      <v-layout row wrap>
                  </v-layout>
                </v-flex>

                <v-flex d-flex md12>
                  <v-layout row wrap>
                    <v-flex d-flex xs12>
                        <v-select
                          v-model="select"
                          :items="items"
                          label=""
                          prepend-icon="people"
                          chips
                          tags
                        ></v-select>
                    </v-flex>
                  </v-layout>
                </v-flex>
                  <v-flex d-flex md12>
                  <v-layout row wrap>
                    <v-flex d-flex xs12>
                        <v-select
                          v-model="select"
                          :items="items"
                          label=""
                          prepend-icon="picture_as_pdf"
                          chips
                          tags
                        ></v-select>
                    </v-flex>
                  </v-layout>
                </v-flex>
              </v-layout>
            </v-container>
          </v-form>
          <v-card-actions>
               <!--  <v-btn :disabled="!formIsValid" flat color="primary" type="submit" class="subheading"></v-btn> -->
               <v-btn flat color="primary" type="submit" class="subheading"></v-btn>
                <v-spacer></v-spacer>
        <v-btn flat class="subheading"></v-btn>
        <v-spacer></v-spacer>
         <v-btn flat @click.native="closeDialog" class="subheading"></v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  props: ['dialog','listname','listid'],
  data() {
    const defaultForm = Object.freeze({
      card: {
        title: '',
        desc: '',
        department: '',
        background: '',
        summary: '',
        induction: ''
      }
    })
    return {
      snackbar: false,
      valid:false,
      titleRules: [
        v => !!v || '!',
        v => v.length <= 20 || '20!'
      ],
      select: [],
      items: [
        'Programming',
        'Design',
        'Vue',
        'Vuetify',
      ],
      form: Object.assign({}, defaultForm),
    }
  },
  methods: {
    submit: function() {
      let that = this
      Trello.post('cards', {'name': this.form.card.title, 'idList': this.form.card.list } , function() {
        
      })
    },
    closeDialog: function() {

    }
  }
}
</script>

<style>

</style>



